import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable({ providedIn: 'root' })
export class ValidationService {
    private userSubject: BehaviorSubject<User>;
    public user: Observable<User>;

    constructor(
        private router: Router,
        private http: HttpClient,
    ) {
        this.userSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('user')));
        this.user = this.userSubject.asObservable();
    }

    getApplication() {
        return this.http.get<User[]>(`${environment.apiUrl}/validation/application`);
    }

    getQuestions() {
        return this.http.get<User[]>(`${environment.apiUrl}/validation/questions`);
    }

    checkQuestion(id, answer) {
        return this.http.post<User[]>(`${environment.apiUrl}/validation/question/check`, {id: id, answer: answer});
    }

    sendValidation(params) {
        return this.http.post<User[]>(`${environment.apiUrl}/validation/send`, params);
    }
}