import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map, first } from 'rxjs/operators';

import { createAvatar } from '@dicebear/avatars';
import * as styleAvatar from '@dicebear/pixel-art';

import { FileUploadService } from './fileUpload.service';
import { webSocketService } from './webSocket.service';
import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable({ providedIn: 'root' })
export class AccountService {
    private userSubject: BehaviorSubject<User>;
    public user: Observable<User>;

    constructor(
        private router: Router,
        private http: HttpClient,
        private wsService: webSocketService,
        private FileUploadService: FileUploadService
    ) {
        this.userSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('user')));
        this.user = this.userSubject.asObservable();
    }

    public get userValue(): User {
        return this.userSubject.value;
    }

    public connect(user) {
        this.wsService.connect(user);
    }

    login(username, password) {
        return this.http.post<User>(`${environment.apiUrl}/users/authenticate`, { username, password })
            .pipe(map(user => {
              this.FileUploadService.getAvatar(user.id)
                .pipe(first())
                .subscribe(avatar => {
                    const svgAvatar = createAvatar(styleAvatar, {
                        seed: user.username,
                        backgroundColor: '#868889',
                    });

                    if(avatar != '') {
                        user['avatar'] = environment.apiUrl+avatar;
                    } else {
                        user['avatar'] = '';
                        user['svg'] = svgAvatar;
                    }
                    this.FileUploadService.getCover(user.id)
                      .pipe(first())
                      .subscribe(cover => {
                        if(cover != '') {
                          user['cover'] = environment.apiUrl+cover;
                        } else {
                          user['cover'] = '';
                        }
                        
                        this.connect(user);
                        localStorage.setItem('user', JSON.stringify(user));
                        this.userSubject.next(user);
                      });
                });
                return user;
            }));
    }

    logout() {
        localStorage.removeItem('user');
        this.userSubject.next(null);
        this.router.navigate(['/auth/login']);
        this.wsService.disconnect();
    }

    register(user) {
        return this.http.post(`${environment.apiUrl}/users/register`, user);
    }

    getAll() {
        return this.http.get<User[]>(`${environment.apiUrl}/users`);
    }

    getById(id: string) {
        return this.http.get<User>(`${environment.apiUrl}/users/${id}`);
    }

    getByUsername(username: string) {
        return this.http.get(`${environment.apiUrl}/users/username/${username}`);
    }

    getByIdForTopic(id) {
        return this.http.get<User>(`${environment.apiUrl}/users/topic/${id}`);
    }

    update(id, params) {
        return this.http.put(`${environment.apiUrl}/users/${id}`, params)
            .pipe(map(x => {
                if (id == this.userValue.id) {
                    const user = { ...this.userValue, ...params };
                    localStorage.setItem('user', JSON.stringify(user));

                    // publish updated user to subscribers
                    this.userSubject.next(user);
                }
                return x;
            }));
    }

    delete(id: string) {
        return this.http.delete(`${environment.apiUrl}/users/${id}`)
            .pipe(map(x => {
                if (id == this.userValue.id) {
                    this.logout();
                }
                return x;
            }));
    }
    
    createCharacter(params) {
        return this.http.post(`${environment.apiUrl}/characters/create`, params);
    }
}