import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';

import { webSocketService } from './webSocket.service';
import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable({ providedIn: 'root' })
export class chatService {
    private userSubject: BehaviorSubject<User>;
    public user: Observable<User>;
    messages: Subject<any>;
    connected: Subject<any>; 
    connected_list: Subject<any>;

    constructor(
        private router: Router,
        private http: HttpClient,
        private wsService: webSocketService
    ) {
        this.userSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('user')));
        this.user = this.userSubject.asObservable();
        
        this.messages = <Subject<any>>wsService
          .ObserveMessage()
          .pipe(map((response: any): any => {
            return response;
          }));

        this.connected = <Subject<any>>wsService
          .checkOnline()
          .pipe(map((response: any): any => {
            return response;
          }));

        this.connected_list = <Subject<any>>wsService
          .connectedList()
          .pipe(map((response: any): any => {
            return response;
          }));
    }
    
    sendMsg(msg) {
        this.messages.next(msg);
    }

    getPm(from, to) {
        return this.http.get<User[]>(`${environment.apiUrl}/messaging/to/${to}/from/${from}`);
    }

    socketConnected(id) {
        this.connected.next(id);
    }

    getConnectedList() {
        this.connected_list.next();
    }

    fetchLast(id) {
        return this.http.get<User[]>(`${environment.apiUrl}/messaging/last/${id}`);
    }
}