import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';

import { webSocketService } from './webSocket.service';
import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable({ providedIn: 'root' })
export class forumService {
    private userSubject: BehaviorSubject<User>;
    public user: Observable<User>;
    messageTopic: Subject<any>;

    constructor(
        private router: Router,
        private http: HttpClient,
        private wsService: webSocketService
    ) {
        this.userSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('user')));
        this.user = this.userSubject.asObservable();

        this.messageTopic = <Subject<any>>wsService
          .topicObserve()
          .pipe(map((response: any): any => {
            return response;
          }));
    }

    getAllCats() {
        return this.http.get(`${environment.apiUrl}/forums/categories`);
    }

    getTopics(id) {
        return this.http.get(`${environment.apiUrl}/forums/topics/forum/${id}`);
    }

    getPosts(id) {
        return this.http.get(`${environment.apiUrl}/forums/posts/topic/${id}`);
    }

    getAllforumParent() {
        return this.http.get(`${environment.apiUrl}/forums`);
    }

    post(id, subject) {
        return this.http.post<User[]>(`${environment.apiUrl}/forums/posts/f/${id}`, subject);
    }

    respondTopic(id, message) {
        this.messageTopic.next({id: id, message: message});
        return this.http.post<User[]>(`${environment.apiUrl}/forums/posts/t/${id}`, message);
    }
}