import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable({
  providedIn: 'root'
})
export class FileUploadService {
    private userSubject: BehaviorSubject<User>;
    public user: Observable<User>;
  constructor(private http:HttpClient) { 
        this.userSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('user')));
        this.user = this.userSubject.asObservable();
  }
  
  upload(file, userid):Observable<any> {
      const formData = new FormData(); 
      formData.append("file", file, file.name);

      return this.http.post(`${environment.apiUrl}/users/image/upload/avatar/${userid}`, formData);
  }

  getAvatar(userid):Observable<any> {
      return this.http.get(`${environment.apiUrl}/users/image/avatar/${userid}`);
  }

  getCover(userid):Observable<any> {
      return this.http.get(`${environment.apiUrl}/users/image/cover/${userid}`);
  }
}